
const { ccclass, property } = cc._decorator;
// ts自写初始垃圾轮盘方法

@ccclass
export default class lunpan extends cc.Component {

    fViewHg: number = null;
    fViewWh: number = null;
    zhujNode: cc.Node = null;

    isMove: boolean = false;
    @property
    speed: number = 10;

    onLoad() {
        this.fViewHg = this.node.parent.height;
        this.fViewWh = this.node.parent.width;
        this.zhujNode = cc.find("Canvas/zhujue");
        // console.log(this.fViewHg, this.fViewWh);
    }
    start() {
        // 触摸事件
        this.node.on(cc.Node.EventType.TOUCH_START, () => { //按下
            this.isMove = true;
        });
        this.node.on(cc.Node.EventType.TOUCH_END, () => { // 离开
            this.isMove = false;
            this.node.setPosition(50, 50);
        });
        this.node.on(cc.Node.EventType.TOUCH_MOVE, (e: cc.Event.EventMouse) => {  //持续按下
            // console.log(e);
            if (this.isMove) {
                let parentPos = this.node.parent.convertToNodeSpaceAR(e.getLocation());
                // console.log("父节点内鼠标坐标：", parentPos);
                this.node.setPosition(parentPos.x, parentPos.y);
                // console.log(this.node.x);
                if (this.node.x - this.node.width / 2 < 0) {
                    this.node.x = this.node.width / 2;
                } else if (this.node.x + this.node.width / 2 > this.fViewWh) {
                    this.node.x = this.fViewWh - this.node.width / 2;
                }
                if (this.node.y - this.node.height / 2 < 0) {
                    this.node.y = this.node.height / 2;
                } else if (this.node.y + this.node.height / 2 > this.fViewHg) {
                    this.node.y = this.fViewHg - this.node.height / 2;
                }
            }
        })

    }
    update(dt: number) {
        console.log("节点位置x", this.node.x);
        if (this.node.x > 50) {
            this.zhujNode.x += this.speed * dt
        } else if (this.node.x < 50) {
            this.zhujNode.x -= this.speed * dt
        }
        if (this.node.y > 50) {
            this.zhujNode.y += this.speed * dt
        } else if (this.node.y < 50) {
            this.zhujNode.y -= this.speed * dt
        }

    }
}


